<template>
        <v-app>
            <v-content>
                <v-container class="fill-height" fluid >
                    <v-row  justify="center">
                        <v-col cols="11">
                            <v-card>
                                <v-img :src="require('@/assets/loginBackground.png')">
                                <v-row>
                                    <v-col cols="5">
                                    </v-col>
                                    <v-col cols="6" >
                                        <div class="header" >
                                            <div id="title">
                                                LetsEat
                                            </div>
                                            <div id="tagline">
                                                Login to your account
                                            </div>
                                        </div>
                                        <div class="form">
                                            <v-text-field
                                                v-model="form.email"
                                                label="Email"
                                                filled
                                                rounded
                                                prepend-inner-icon="mdi-human-male"
                                            ></v-text-field>
                                            <v-text-field
                                                v-model="form.password"
                                                label="Password"
                                                filled
                                                rounded
                                                dense
                                                prepend-inner-icon="mdi-shield-lock"
                                                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                                                :type="show1 ? 'text' : 'password'"
                                                @click:append="show1 = !show1"
                                            ></v-text-field>
                                            <v-checkbox v-model="checkbox" style="margin-top : -10px">
                                                <template v-slot:label>
                                                    <div>
                                                        Remember Login
                                                    </div>
                                                </template>
                                                </v-checkbox>
                                                <v-btn  @click="login()" block color="yellow" rounded  class="elevation-0" height=50>Login</v-btn>
                                                <h4 style="margin-top: 20px;" >Dont have and account? <a style="color:blue" @click="register()" >Register Now!</a> </h4>
                                                <a  @click="home()" style=" color:blue"  >Back Home</a>
                                        </div>
                                    </v-col>
                                    
                                </v-row>
                                </v-img>
                            </v-card>
                        </v-col>
                    </v-row>
                </v-container>
            </v-content>
        </v-app>
</template>

<script>
export default {
    data(){
        return {
            show1: false,
            form : {
                email: null, 
                password: null,
            },
            user : new FormData,
        }
    },
    methods:{
        home(){
             this.$router.push({ name : "landingPage"})
        },
        register(){
             this.$router.push({ name : "registerLayout"})
        },
        login(){
            var url = this.$apiUrl + '/Auth'
            this.user = new FormData()
            this.user.append('email',this.form.email);
            this.user.append('password',this.form.password);
            this.$http.post(url,this.user).then(response =>{
                if(response.data.token){
                    localStorage.setItem("token" , response.data.token)
                    localStorage.setItem("id" , response.data.id)
                    localStorage.setItem("role" , response.data.role)
                this.$router.push({ name : "Dashboard"})
                
                }else{
                    alert('failed login')
                }
            })
        }
    },
    mounted(){        
        if(localStorage.getItem('token') != null){
            this.$router.push({ name : "logoutGuard"})
        }     
    },

}
</script>

<style>
.header{
    margin-left: 75px;
    margin-top: 200px;
    margin-bottom: -30px;
    color: #000000;
    /* margin-bottom: 690px;
    margin-right: 654px; */
}
#title{
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 50px;
    line-height: 37px;
}
#tagline{
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-weight: 300;
    font-size: 30px;
    line-height: 33px;
}
.form{
    margin-left: 76px;
    margin-top: 70px;
    margin-bottom: 120px;
    color: rgba(0, 0, 0, 0.54);
}
</style>