<template>     
    <div>         
        <v-navigation-drawer         
        v-model="drawer"                   
        app         
        clipped 
        dark        
        fixed         
        temporary         
        ><v-list-item>                 
            <img src="../assets/logoListDashboard.png" style="height:200px;width:200px; margin-top:20px;margin-bottom:30px; margin-left:10px;">          
                    </v-list-item> 
 
            <v-divider></v-divider>             
            <v-list
            v-for="item in items"  
            :key="item.title" >
                <v-divider v-if="item.div"></v-divider>                   
                <v-list-item                   
                link
                color="yellow"       
                :to="item.router"          
                >   
                <v-list-item-icon>                     
                        <v-icon>{{ item.icon }}</v-icon>                 
                    </v-list-item-icon> 
 
                    <v-list-item-content>                     
                        <v-list-item-title>{{ item.title }}</v-list-item-title>               
                    </v-list-item-content>
                    
                                 
                </v-list-item>            
                </v-list>         
            <template v-slot:append>             
                <div class="pa-2">             
                    <v-btn color="yellow" @click="logout()" block>Logout</v-btn>             
                </div>         
            </template>         
            </v-navigation-drawer> 
 
        <v-app-bar             
        app             
        fixed  
        color="yellow"           
        clipped-left             
        height="70px"        
        >             
        <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon> 
 
        <VSpacer />                                       
        
        <img src="../assets/logoDashboard.png" style="height:45px;width:200px">                          
        
         </v-app-bar> 
 
        <VContent >             
            <router-view />         
        </VContent>     
    </div> 
</template> 
 
<script>   
    export default {     
        data () {       
            return {         
                drawer: null,         
                items: [],       
            }     
        },
        methods: {
            setList1(){
                this.items = [
                    { title: 'Dashboard', icon: 'mdi-view-dashboard', router: "/dashboard", div:false },      
                    { title: 'Transaction', icon: 'mdi-human-male', router: "/transactionController", div:false },   
                    { title: 'Order Table', icon: 'mdi-human-male', router: "/tableController", div:false },   
                    { title: 'Employee', icon: 'mdi-human-male', router: "/employeeController", div:false },   
                    { title: 'Admin List', icon: 'mdi-human-male', router: "/adminController", div:true },   
                    { title: 'Customer List', icon: 'mdi-human-male', router: "/customerController", div:false },   
                    { title: 'Transaction List', icon: 'mdi-human-male', router: "/transactionListController", div:false },   
                    { title: 'Profile', icon: 'mdi-human-male', router: "/profileController", div:true },   
                    { title: 'Change Password', icon: 'mdi-human-male', router: "/changePassword", div:false },   
                    ]  
            },
            setList2(){
                this.items = [           
                    { title: 'Dashboard', icon: 'mdi-view-dashboard', router: "/dashboard", div:false },      
                    { title: 'Transaction', icon: 'mdi-human-male', router: "/transactionController", div:false },   
                    { title: 'Order Table', icon: 'mdi-human-male', router: "/tableController", div:false },  
                    { title: 'Transaction List', icon: 'mdi-human-male', router: "/transactionListController", div:true },   
                    { title: 'Profile', icon: 'mdi-human-male', router: "/profileController", div:true },   
                    { title: 'Change Password', icon: 'mdi-human-male', router: "/changePassword", div:false },   
                    ]  
            },
            logout()
            {
                localStorage.removeItem("token")
                localStorage.removeItem("id")
                localStorage.removeItem("role")
                this.$router.push({name : "loginLayout"})
            }
        },
        mounted: function() {
            if(localStorage.getItem('role') == "Customer"){
                this.setList2()
            }else
                this.setList1()
        }   
    } 
</script> 
 
 