<template>
        <v-app>
            <v-content>
                <v-container class="fill-height" fluid>
                    <v-row  justify="center">
                        <v-col cols="11">
                            <v-card>
                                <v-img :src="require('@/assets/registerBackground.png')">
                                <v-row>
                                    <v-col cols="6">
                                    </v-col>
                                    <v-col cols="5" >
                                        <div class="form">
                                            <v-text-field
                                                v-model="form.name"
                                                label="Name"
                                                filled
                                                rounded
                                            ></v-text-field>
                                            <v-menu
                                            v-model="menu2"
                                            :close-on-content-click="false"
                                            :nudge-right="40"
                                            transition="scale-transition"
                                            offset-y
                                            min-width="290px"
                                            >
                                            <template v-slot:activator="{ on }">
                                            <v-text-field
                                                v-model="form.birthdate"
                                                label="Birthdate*"
                                                prepend-inner-icon="mdi-calendar"
                                                readonly
                                                filled
                                                rounded
                                                v-on="on"
                                            ></v-text-field>
                                            </template>
                                            <v-date-picker color="yellow" v-model="form.birthdate" @input="menu2 = false"></v-date-picker>
                                            </v-menu>
                                            <v-select
                                                v-model="form.gender"
                                                :items="items"
                                                label="Gender*"
                                                filled
                                                rounded
                                                dense
                                            ></v-select> 
                                            <v-text-field
                                                v-model="form.address"
                                                label="Address"
                                                filled
                                                rounded
                                            ></v-text-field> 
                                            <v-text-field
                                                v-model="form.email"
                                                label="Email"
                                                filled
                                                rounded
                                            ></v-text-field>
                                            <v-text-field
                                                v-model="form.password"
                                                label="Password"
                                                filled
                                                rounded
                                                dense
                                                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                                                :type="show1 ? 'text' : 'password'"
                                                @click:append="show1 = !show1"
                                            ></v-text-field>
                                            
                                                <v-btn  @click="sendData()" block color="yellow" rounded  class="elevation-0" height=50>REGISTER</v-btn>
                                                <h4 style="margin-top: 20px;" >Have an account? <a style="color:blue" @click="toLogin()" >Login!</a></h4>
                                                <h4  @click="home()" style=" color:blue" >Back Home</h4>
                                        </div>
                                    </v-col>
                                    
                                </v-row>
                                </v-img>
                            </v-card>
                        </v-col>
                    </v-row>
                </v-container>
            </v-content>
        </v-app>
</template>

<script>
export default {
    data(){
        return {
            edit: false,
            menu2: false,
            show: false,      
            show1: false,      
            show2: false,      
            dialog: false,  
            pass: false,       
            keyword: '', 
            items: ['Male', 'Female'],
            form : {
                email: null, 
                password: null,
                name: null,
                gender: null,
                birthdate: null,
                address: null,
            },
            user : new FormData,
        }
    },
    methods:{
        home(){
             this.$router.push({ name : "landingPage"})
        },
        toLogin(){
             this.$router.push({ name : "loginLayout"})
        },
        sendData(){
            var url = this.$apiUrl + '/User/register'
            this.user = new FormData()
            this.user.append('name',this.form.name);
            this.user.append('birthdate',this.form.birthdate);
            this.user.append('gender',this.form.gender);
            this.user.append('address',this.form.address);
            this.user.append('email',this.form.email);
            this.user.append('password',this.form.password);
            this.$http.post(url,this.user).then(response =>{
                if(response.data.message == "Berhasil"){
                    alert('Please Check Your email for verification!')
                this.$router.push({ name : "loginLayout"})
                }else{
                    alert('Your account has been registered')
                }
            })
        }
    },
    mounted(){        
        if(localStorage.getItem('token') != null){
            this.$router.push({ name : "logoutGuard"})
        }     
    },

}
</script>

<style>
.header{
    margin-left: 75px;
    margin-top: 200px;
    margin-bottom: -30px;
    color: #000000;
    /* margin-bottom: 690px;
    margin-right: 654px; */
}
#title{
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 50px;
    line-height: 37px;
}
#tagline{
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-weight: 300;
    font-size: 30px;
    line-height: 33px;
}
.form{
    margin-left: 76px;
    margin-top: 70px;
    margin-bottom: 120px;
    color: rgba(0, 0, 0, 0.54);
}
</style>